import md5 from '../../node_modules/md5';
export default {
	data() {
		return {
			isLogin: true,
			username: '',
			password: '',
			passwordConfirm: '',
			code: '',
			isWait: false,
			waitSecond: 60,
		}
	},
	methods: {
		submit() {
			if (this.isLogin) {
				this.login()
			} else {
				this.registered()
			}
		},
		login() {
			this.requests({
				url: 'user/login',
				data: {
					userName: this.username,
					password: md5(this.password)
				},
				success: (res) => {
					uni.setStorageSync('email',res.data.email);
					uni.setStorageSync('username',res.data.name);
					uni.setStorage({
						key: 'token',
						data: res.data.token,
						success: () => {
							this.updateToken();
							this.toast(res.data.message || res.data);
							uni.reLaunch({
								url: '../main/main'
							});
						}
					});
				}
			})
		},
		 registered() {
			if (this.password !== this.passwordConfirm) {
				this.toast("两次密码不一致");
				return;
			}
			if (!this.isEmail(this.username)) {
				this.toast("请输入正确邮箱地址");
				return;
			}
			if (!this.isValidPassword(this.password)) {
				this.toast("请输入6-15位数字,字母和下划线组成的密码");
				return;
			}






























				this.requests({
					url: 'user/registered',
					data: {
						email: this.username,
						code: this.code,
						password: md5(this.password)
						// brand: infos.brand,
						// model: infos.model,
						// storage: infos.storage,
						// system: infos.system,
						// platform: infos.platform,
						// cacheLocation: infos.cacheLocation
					},
					success: (res) => {
						this.toast("注册成功!跳转登录...");
						this.isLogin = true;
					}
				})

		
		},
		sendEmail() {
			if (this.isWait) {
				return;
			}
			if (!this.isEmail(this.username)) {
				this.toast("请输入正确邮箱地址");
				return;
			}
			this.requests({
				url: 'user/sendMail',
				data: {
					email: this.username
				},
				success: (res) => {
					this.toast("已向" + this.username + "发送了验证码,如未收到请检查邮箱地址.");
				},
				fail: (res) => {

				},
				complete: (res) => {
					this.isWait = true;
					let interval = setInterval(() => {
						if (this.waitSecond-- <= 0) {
							this.waitSecond = 60;
							this.isWait = false;
							clearInterval(interval);
						}
					}, 1000);
				}
			});
		},

		isValidPassword(password) {
			let reg = /^[\w]{6,15}$/;
			return reg.test(password)
		}
	}
}
